@import './var';

.menu-container {
  background: #fff;
  overflow: hidden;
  padding: 10px 0;
  @media (max-width: 359px) {
    padding: 5px 0;
  }
  .menuRouteAnimation {
    position: relative;
    .fade {
      opacity: 0;
      z-index: 1;
    }
    .fade-enter {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      // bottom: 0;
      opacity: 1;
      transition: opacity 300ms linear 150ms;
    }

    .fade-enter.fade-enter-active {
      opacity: 1;
    }
    .fade-exit {
      opacity: 1;
    }
    .fade-exit.fade-exit-active {
      opacity: 0;
      transition: opacity 150ms linear;
    }
  }

  .menuContentBox {
    // position: relative;
    // position: absolute;
    // margin: 10px auto;
    // @media (max-width: 359px) {
    //   margin: 5px auto;
    // }
    h1 {
      /* ${props => props.theme.fontLeckerli} */
      // font-family: $fontRobotoSlab;
      // font-size: 40px;
      color: $darkNavy;
      // color: $darkRed;
      text-align: center;
      font-family: $fontAmatic;
      font-size: 64px;
      font-weight: 700;
      line-height: 1.4;
      @media (max-width: 767px) {
        font-size: 44px;
      }
    }
    .cashOnly {
      font-size: 14px;
      position: absolute;
      top: 0;
      right: 0.5%;
      font-style: italic;
      // color: $darkNavy;
      color: $lightNavy;
      line-height: 0.5;
    }
    .itemsBox {
      margin-top: 10px;
      padding: 20px 100px;
      @media (max-width: 1023px) {
        padding: 40px;
      }
      @media (max-width: 767px) {
        padding: 10px;
      }
      @media (max-width: 359px) {
        // padding: 4px;
        padding: 6px;
      }
      .itemBoxGrid {
        display: grid;
        grid-gap: 20px;
        grid-template-columns: 1fr 1fr;

        @media (max-width: 767px) {
          grid-gap: 10px;
          grid-template-columns: 1fr;
        }

        /* grid-template-columns: repeat(auto-fit, minmax(400px, 1fr)); */

        .itemBox {
          /* background: rgba(255, 0, 0, 0.5); */
          display: flex;
          justify-content: center;
          .imageBox {
            overflow: hidden;
            width: 160px;
            height: 160px;
            // border-radius: 80px;
            backface-visibility: hidden;
            position: relative;
            .popup-box {
              // transform: scale(0);
              display: flex;
              justify-content: center;
              align-items: center;
              position: fixed;
              background: rgba(0, 0, 0, 0.93);
              z-index: 200;
              // margin-left: -8px;
              // top: 50%;
              // left: 50%;
              // transform: translate(-50%, -50%);
              // width: 80%;
              // height: 60%;
              width: 100%;
              height: 100%;
              left: 0;
              top: 0;
              right: 0;
              transition: transform 1000ms;
              @media (orientation: portrait) {
                // width: 96%;
                // height: 70%;
              }
              .contentBox {
                margin-left: 0;
                margin-bottom: 10px;
                padding: 10px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .title {
                  color: $offWhite;
                  line-height: 1;
                  margin-bottom: 10px;
                  @media (max-width: 900px) and (orientation: landscape) {
                    font-size: 34px;
                    margin-bottom: 5px;
                  }
                  // position: relative;
                  // .priceBox {
                  //   position: absolute;
                  //   bottom: 70%;
                  //   left: 96%;
                  //   .priceDollar,
                  //   .price {
                  //     font-size: 20px;
                  //     color: $darkRed;
                  //   }
                  //   .priceDollar {
                  //     font-family: $fontRobotoSlab;
                  //   }
                  //   .price {
                  //     font-family: $fontPlayfair;
                  //   }
                  // }
                }
                .img-box {
                  backface-visibility: hidden;
                  overflow: hidden;
                  img {
                    backface-visibility: hidden;
                    width: 100%;
                    height: auto;
                    border-radius: 40px;
                    max-width: 800px;
                    @media (max-width: 900px) and (orientation: landscape) {
                      max-width: 300px;
                    }
                  }
                }
                .desc {
                  color: #fff;
                  text-align: center;
                  @media (max-width: 900px) and (orientation: landscape) {
                    margin-top: -10px;
                  }
                }
                .priceBox {
                  line-height: 1;
                  .priceDollar,
                  .price {
                    font-size: 20px;
                    color: $red;
                  }
                  .priceDollar {
                    font-family: $fontRobotoSlab;
                  }
                  .price {
                    font-family: $fontPlayfair;
                  }
                }
              }
              .close {
                position: absolute;
                right: 2%;
                top: 2%;
                padding: 0;
                margin: 0;
                border: 0;
                outline: 0;
                cursor: pointer;
                font-size: 80px;
                color: $offWhite;
              }
            }
            // .expand-button {
            //   position: absolute;
            //   left: 0;
            //   top: 0;
            //   color: #555;
            //   cursor: pointer;
            // }
            @media (max-width: 1023px) {
              width: 140px;
              height: 140px;
              // border-radius: 70px;
              img {
                border-radius: 70px;
              }
            }
            /* width: 200px;
            height: 140px;
            border-radius: 8px; */
            /* position: relative; */
            img {
              /* position: absolute;
              left: 50%;
              transform: translateX(-50%); */
              // -webkit-transform: translateZ(0);
              // -webkit-perspective: 1000;
              backface-visibility: hidden;
              // width: 160px;
              // height: 160px;
              border-radius: 80px;
              width: 100%;
              height: 100%;
              user-select: none;
              filter: brightness(110%);
              // @media (max-width: 1023px) {
              //   width: 140px;
              //   height: 140px;
              //   border-radius: 70px;
              // }
            }
          }
          .contentBox {
            flex: 1;
            margin-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @media (max-width: 767px) {
              margin-left: 10px;
            }
            .name {
              position: relative;
              // font-family: $fontPlayfair;
              font-family: $fontRobotoSlab;
              // ${props => props.theme.fontPlayfair}
              color: $black;
              font-weight: bold;
              font-size: 18px;
              line-height: 1.5;

              .nameDescription {
                position: absolute;
                font-size: 10px;
                // top: 30%;
                top: 40%;
                margin-left: 4px;
                // color: lighten($darkNavy, 10%);
                color: $lightNavy;
                @media (max-width: 359px) {
                  font-size: 9px;
                  margin-left: 2px;
                  // top: 35%;
                  top: 40%;
                  line-height: 1;
                }
              }
              .day {
                position: absolute;
                font-size: 12px;
                font-style: italic;
                // margin-top: -12px;
                margin-top: -18px;

                top: 0;
                left: 10%;
                padding: 1px 6px;
                border-radius: 9px;

                background: $green;
                color: $offWhite;
                // @media (max-width: 1024px) {
                //   margin-top: -18px;
                //   left: 55%;
                // }
                // @media (max-width: 767px) {
                //   margin-top: -18px;
                //   left: 0;
                // }
                // @media (max-width: 565px) {
                //   margin-top: -16px;
                //   left: 35%;
                // }
                // @media (max-width: 359px) {
                //   left: 25%;
                // }
              }
            }
            .price {
              font-size: 18px;
              font-weight: bold;
              font-family: $fontPlayfair;
              // ${props => props.theme.fontPlayfair}
              // color: ${props => props.theme.darkRed};
              color: lighten($darkRed, 5%);

              .striked {
                font-family: $fontRobotoSlab;
                color: $darkRed;
                text-decoration: line-through;
              }

              .dollar-sign {
                font-family: $fontRobotoSlab;
                margin-right: 2px;
              }
            }
            .description {
              color: $grey;
              font-style: italic;
              line-height: 1.2;
            }
          }
        }
      }
    }
  }
}
